/* eslint-disable @typescript-eslint/no-explicit-any */
/* eslint-disable max-len */
/* eslint-disable no-console */
/* eslint-disable no-param-reassign */
/* eslint-disable @typescript-eslint/no-unused-vars */

import { createSlice, PayloadAction, createAsyncThunk } from '@reduxjs/toolkit';
import { IUser } from '../../models/IUser';
import { AuthResponse } from '../../models/response/AuthResponse';
import AuthService from '../../api/services/AuthService';

const initialState = {
  user: {} as IUser,
  isUserAuth: false,
  isUsersLoading: true,
  isLocalUser: false,
};

export interface UsersSliceState {
  user: IUser;
  isUserAuth: boolean;
  isUsersLoading: boolean;
  isLocalUser: false;
}

export const login = createAsyncThunk(
  'login',
  async (payload: { email: string; password: string }, { rejectWithValue }) => {
    try {
      console.log('payload', payload);
      const response = await AuthService.login(payload.email, payload.password);

      console.log('response', response);

      localStorage.setItem('token', response.data.accessToken);

      return response.data.user;
    } catch (e: any) {
      return rejectWithValue(e.message);
    }
  },
);

export const registration = createAsyncThunk(
  'registration',
  async (payload: { name: string, role: string, email: string; password: string }, { rejectWithValue }) => {
    try {
      console.log('name', payload.name, 'role', payload.role, 'email', payload.email, 'password', payload.password);

      const response = await AuthService.registration(payload.name, payload.role, payload.email, payload.password);

      console.log('response', response);

      if (response.status === 200) {
        localStorage.setItem('token', response.data.accessToken);

        return response.data.user;
      }

      return rejectWithValue(response.data);
    } catch (e: any) {
      return rejectWithValue(e.message);
    }
  },
);

export const logout = createAsyncThunk(
  'logout',
  async () => {
    try {
      await AuthService.logout();

      localStorage.removeItem('token');
    } catch (e: any) {
      console.log(e.message);
    }
  },
);

export const checkAuth = createAsyncThunk(
  'checkAuth',
  async (_, { rejectWithValue }) => {
    try {
      const response = await AuthService.checkAuth();

      console.log('response', response);

      localStorage.setItem('token', response.data.accessToken);

      if (response.status === 200) {
        return response.data.user;
      }

      return rejectWithValue(response.data);
    } catch (e: any) {
      return rejectWithValue(e.message);
    }
  },
);

export const usersSlice = createSlice({
  name: 'users',
  initialState,
  reducers: {
    setAuth(state, action: PayloadAction<boolean>) {
      state.isUserAuth = action.payload;
    },

    setUser(state, action: PayloadAction<IUser>) {
      state.user = action.payload;
    },

    setIsUsersLoading(state, action: PayloadAction<boolean>) {
      state.isUsersLoading = action.payload;
    },

    setIsLocalUser(state, action: PayloadAction<boolean>) {
      state.isLocalUser = action.payload;
    },
  },
  extraReducers: (builder) => {
    builder
      .addCase(login.fulfilled, (state, action) => {
        state.isUserAuth = true;
        state.user = action.payload;
      })
      .addCase(login.rejected, (state, action) => {
        console.log(action.payload); // Handle error from rejectWithValue
      });

    builder
      .addCase(registration.fulfilled, (state, action) => {
        state.isUserAuth = true;
        state.user = action.payload;
      })
      .addCase(registration.rejected, (state, action) => {
        console.log(action.payload); // Handle error from rejectWithValue
      });

    builder
      .addCase(logout.fulfilled, (state) => {
        state.isUserAuth = false;
        state.isLocalUser = false;
        state.user = {} as IUser;
      });

    builder
      .addCase(checkAuth.fulfilled, (state, action) => {
        state.isUserAuth = true;
        state.user = action.payload;
      });
  },
});

export const {
  setAuth,
  setUser,
  setIsUsersLoading,
  setIsLocalUser,
} = usersSlice.actions;

export default usersSlice.reducer;
