/* eslint-disable @typescript-eslint/no-explicit-any */
/* eslint-disable jsx-a11y/click-events-have-key-events */
/* eslint-disable jsx-a11y/no-static-element-interactions */
/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable max-len */
import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import s from './ProductCard.module.scss';
import samplePhoto from '../../images/food_photos/foodPhoto.png';
import minusLogo from '../../images/logos/minus-logo.svg';
// import plusIcon from '../../images/logos/plus.svg';
import { Example } from '../../hooks';
import { Product } from '../../types/products';
import { useAppSelector, useAppDispatch } from '../../app/hooks';

import { setProductModalOpen } from '../../features/main/mainSlice';
import { setLocalStorageCart, calculateCartInfo } from '../../features/products/productsSlice';
import likeIcon from '../../images/logos/heart-icon.svg';

type Props = {
  product: Product,
};

export const ProductCard: React.FC<Props> = ({ product }) => {
  const { isMobile } = Example();

  const dispatch = useAppDispatch();

  const isProductModalOpen = useAppSelector((state) => state.main.isProductModalOpen);
  const isUserAuth = useAppSelector((state) => state.users.isUserAuth);
  const localStorageCart = useAppSelector((state) => state.products.localStorageCart);

  const navigate = useNavigate();

  const handleProductClick = () => {
    // eslint-disable-next-line no-console
    console.log('product clicked');

    dispatch(setProductModalOpen(true));
  };

  const handleAddProductToCart = (e: React.MouseEvent<HTMLButtonElement>) => {
    e.stopPropagation();

    if (!isUserAuth) {
      if (localStorage.getItem('cart') === null) {
        localStorage.setItem('cart', JSON.stringify([{
          ...product,
          count: 1,
        }]));

        dispatch(setLocalStorageCart([{
          ...product,
          count: 1,
        }]));
      } else {
        const cart = JSON.parse(localStorage.getItem('cart') as string);

        const isProductInCart = cart.find((item: Product) => item.id === product.id);

        if (isProductInCart) {
          const newCart = cart.map((item: Product) => {
            if (item.id === product.id) {
              return {
                ...item,
                count: item.count + 1,
              };
            }

            return item;
          });

          localStorage.setItem('cart', JSON.stringify(newCart));

          dispatch(setLocalStorageCart(newCart));

          // eslint-disable-next-line no-console
          console.log('product already in cart count increased by 1');
        } else {
          cart.push({
            ...product,
            count: product.count + 1,
          });

          localStorage.setItem('cart', JSON.stringify(cart));

          dispatch(setLocalStorageCart(cart));

          // eslint-disable-next-line no-console
          console.log('product added to cart');
        }
      }
    } else {
      // set product to cart on server
    }

    dispatch(calculateCartInfo({ isUserAuth }));
  };

  const handleDecreaseProductsCount = (e: React.MouseEvent<HTMLButtonElement>) => {
    e.stopPropagation();

    if (!isUserAuth) {
      if (localStorage.getItem('cart') === null) {
        // eslint-disable-next-line no-console
        console.log('cart is empty');
      } else {
        const cart = JSON.parse(localStorage.getItem('cart') as string);

        const isProductInCart = cart.find((item: Product) => item.id === product.id);

        if (isProductInCart) {
          const newCart = cart.map((item: Product) => {
            if (item.id === product.id) {
              return {
                ...item,
                count: item.count - 1,
              };
            }

            return item;
          });

          localStorage.setItem('cart', JSON.stringify(newCart));

          dispatch(setLocalStorageCart(newCart));

          // eslint-disable-next-line no-console
          console.log('product already in cart count decreased by 1');
        } else {
          // eslint-disable-next-line no-console
          console.log('product not in cart');
        }
      }
    } else {
      // remove product from cart on server
    }

    dispatch(calculateCartInfo({ isUserAuth }));
  };

  const handleRemoveProductFromCart = (e: React.MouseEvent<HTMLButtonElement>) => {
    e.stopPropagation();

    if (!isUserAuth) {
      if (localStorage.getItem('cart') === null) {
        // eslint-disable-next-line no-console
        console.log('cart is empty');
      } else {
        const cart = JSON.parse(localStorage.getItem('cart') as string);

        const isProductInCart = cart.find((item: Product) => item.id === product.id);

        if (isProductInCart) {
          const newCart = cart.filter((item: Product) => item.id !== product.id);

          localStorage.setItem('cart', JSON.stringify(newCart));

          dispatch(setLocalStorageCart(newCart));

          // eslint-disable-next-line no-console
          console.log('product removed from cart');
        } else {
          // eslint-disable-next-line no-console
          console.log('product not in cart');
        }
      }
    } else {
      // remove product from cart on server
    }

    dispatch(calculateCartInfo({ isUserAuth }));
  };

  const findProductInCart = () => {
    if (localStorage.getItem('cart') === null) {
      return false;
    }

    const cart = JSON.parse(localStorage.getItem('cart') as string);

    const isProductInCart = cart.find((item: Product) => item.id === product.id);

    if (isProductInCart?.count >= 1) {
      return isProductInCart;
    }

    return isProductInCart;
  };

  const handleLikeClick = (e: React.MouseEvent<HTMLButtonElement>) => {
    e.stopPropagation();
  };

  return (
    <div
      className={s.product__card}
      onClick={() => (handleProductClick())}
    >
      <div className={s.product__card_container}>
        <div className={s.product__card_left}>
          <div className={s.product__card_left__name}>
            {product.name}
          </div>
          <div className={s.product__card_left__price}>
            {`${product.price} $`}
          </div>

          <p className={s.product__card_left__description}>
            {`${product.description}`}
          </p>

          <div className={s.product__card_left__label}>
            <div className={s.product__card_left__label__weight} />

            <p className={s.product__card_left__label__text}>
              {`${product.weight}g`}
            </p>
          </div>

          <button
            type="button"
            className={s.product__card_left__button}
            onClick={(e) => (handleLikeClick(e))}
          >
            <div className={s.product__card_left__button_wrapper}>
              <img
                src={likeIcon}
                alt="like-icon"
                className={s.product__card_left__button__like_icon}
              />
              <div className={s.product__card_left__button__like_count}>
                {product.likesCount}
              </div>
            </div>
          </button>
        </div>

        <div className={s.product__card__right}>
          <img
            src={samplePhoto}
            alt="no_image"
            className={s.product__card__right__image}
          />
          {!(findProductInCart()?.count > 0) ? (
            <button
              type="button"
              className={s.product__card__right__button}
              onClick={(e) => (handleAddProductToCart(e))}
            >
              <div className={s.product__card__right__button__add_icon} />
              <span
                className={s.product__card__right__button__text}
              >
                ADD
              </span>
            </button>
          ) : (
            <div className={s.button__add}>
              {findProductInCart()?.count > 1 ? (
                <button
                  type="button"
                  className={s.button__add__minus}
                  onClick={(e) => (handleDecreaseProductsCount(e))}
                >
                  <img src={minusLogo} alt="minus-logo" className={s.button__add__minus__icon} />
                </button>
              ) : (
                <button
                  type="button"
                  className={s.button__add__delete}
                  onClick={(e) => (handleRemoveProductFromCart(e))}
                >
                  <div className={s.button__add__delete__icon} />
                </button>
              )}

              <span
                className={s.button__add__count}
              >
                {findProductInCart()?.count}
              </span>

              <button
                className={s.button__add__plus}
                type="button"
                onClick={(e) => (handleAddProductToCart(e))}
              >
                <div className={s.button__add__plus__icon} />
              </button>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};
