/* eslint-disable no-console */
/* eslint-disable react-hooks/rules-of-hooks */
import ProductService from '../api/services/ProductService';
import {
  getProductsStart,
  getProductsSuccess,
  getProductsFailure,
  setProductsLength,
  setErrorText,
} from '../features/products/productsSlice';
import { Product, ErrorType } from '../types/products';

export const getAndSetProducts = (
  dispatch: any,
  currentPage: number,
  itemsPerPage: number,
  selectedFilter: string,
  searchQuery: string,
): void => {
  console.log('selectedFilter', selectedFilter);
  dispatch(getProductsStart());

  ProductService.getProductsChunk(
    currentPage,
    itemsPerPage,
    selectedFilter,
    searchQuery,
  )
    .then((data: Product[]) => {
      // eslint-disable-next-line no-console
      console.log(data);
      dispatch(getProductsSuccess(data));
    })
    .catch(() => {
      dispatch(getProductsFailure(ErrorType.SERVER_ERROR));
    });

  ProductService.getProductsLength(selectedFilter, searchQuery)
    .then((data: number) => {
      console.log('length', data);
      dispatch(setProductsLength(data));
    })
    .catch(() => {
      dispatch(setErrorText('Can not get products length'));
    });
};
