import {
  AsyncThunk,
  AsyncThunkAction,
  createAsyncThunk,
  createSlice,
  PayloadAction,
} from '@reduxjs/toolkit';
import { Product, ErrorType, ProductCategory } from '../../types/products';
import { useAppSelector } from '../../app/hooks';

const initialState = {
  products: [] as Product[],
  isProductsLoading: false,
  error: ErrorType.NULL,
  errorText: '',
  selectedFilter: ProductCategory.ALL,
  itemsPerPage: 16,
  currentPage: 1,
  productsLength: 0,
  searchQuery: '',
  localStorageCart: [] as Product[],
  cartItemsCount: 0,
  cartTotalPrice: 0,
};

export interface ProductsState {
  products: Product[];
  isProductsLoading: boolean;
  error: ErrorType;
  errorText: string;
  selectedFilter: ProductCategory;
  itemsPerPage: number;
  currentPage: number;
  productsLength: number;
  searchQuery: string;
  localStorageCart: Product[];
  cartItemsCount: number;
  cartTotalPrice: number;
}

export const calculateCartInfo = createAsyncThunk(
  'calculateCartInfo',
  async (payload: { isUserAuth: boolean }, { rejectWithValue }) => {
    let total = 0;
    let count = 0;
    const cart = JSON.parse(localStorage.getItem('cart') as string);

    if (payload.isUserAuth) {
      // work with server cart
    } else if (cart) {
      cart.forEach((item: Product) => {
        total += item.price * item.count;
        count += 1;
      });
    } else {
      total = 0;
      count = 0;
    }

    return {
      total,
      count,
    };
  },
  // {
  //   returnType: 'object',
  // },
);

export const productsSlice = createSlice({
  name: 'products',
  initialState,
  reducers: {
    setErrorText(state, action: PayloadAction<string>) {
      // state.errorText = action.payload;
    },

    getProductsStart(state) {
      state.isProductsLoading = true;
    },

    getProductsSuccess(state, action: PayloadAction<Product[]>) {
      state.products = action.payload;
      state.isProductsLoading = false;

      console.log('getProductsSuccess');
    },

    getProductsFailure(state, action: PayloadAction<ErrorType>) {
      state.isProductsLoading = false;
      state.error = action.payload;
    },

    setFilter(state, action: PayloadAction<ProductCategory>) {
      state.selectedFilter = action.payload;

      // Reset current page to 1
      state.currentPage = 1;
    },

    setCurrentPage(state, action: PayloadAction<number>) {
      state.currentPage = action.payload;
    },

    setItemsPerPage(state, action: PayloadAction<number>) {
      state.itemsPerPage = action.payload;

      // Reset current page to 1
      state.currentPage = 1;
    },

    setProductsLength(state, action: PayloadAction<number>) {
      state.productsLength = action.payload;
    },

    setSearchQuery(state, action: PayloadAction<string>) {
      state.searchQuery = action.payload;

      // Reset current page to 1
      state.currentPage = 1;
    },

    setLocalStorageCart(state, action: PayloadAction<Product[]>) {
      state.localStorageCart = action.payload;
    },
  },
  extraReducers: (builder) => {
    builder.addCase(calculateCartInfo.fulfilled, (state, action) => {
      state.cartItemsCount = action.payload.count;
      state.cartTotalPrice = action.payload.total;
    });
  },
});

export const {
  getProductsStart,
  getProductsSuccess,
  getProductsFailure,
  setFilter,
  setCurrentPage,
  setItemsPerPage,
  setProductsLength,
  setErrorText,
  setSearchQuery,
  setLocalStorageCart,
} = productsSlice.actions;

export default productsSlice.reducer;
