/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable jsx-a11y/no-static-element-interactions */
/* eslint-disable no-console */
/* eslint-disable max-len */
import React, { useState, KeyboardEvent } from 'react';
import s from './AuthPage.module.scss';
import googleLogo from '../../images/logos/google-logo.svg';
import facebookLogo from '../../images/logos/facebook-logo.svg';
import plusPeople from '../../images/logos/plus-people.svg';
import arrowRight from '../../images/logos/arrow-oval-right-white.svg';
import manRight from '../../images/logos/man-right.svg';
import exitDoor from '../../images/logos/exit-door.svg';
import { Stage } from '../../types/products';
import { Example } from '../../hooks';
import { login, registration, setIsLocalUser } from '../../features/users/usersSlice';
import { useAppDispatch, useAppSelector } from '../../app/hooks';
import doorLogo from '../../images/logos/door.svg';

export const AuthPage = () => {
  const [stage, setStage] = useState(Stage.LOGIN);
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [name, setName] = useState('');
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [role, setRole] = useState('user');
  // const [isAnimationActive, setIsAnimationActive] = useState(false);

  const dispatch = useAppDispatch();

  const isLocalUser = useAppSelector((state) => state.users.isLocalUser);

  const {
    is900,
  } = Example();

  const handleStageChange = (stageValue: Stage) => {
    const container = document.getElementById('container');

    container?.classList.add(s.active);

    setTimeout(() => {
      container?.classList.remove(s.active);
    }, 2000);

    setStage(stageValue);
    console.log('stageValue', stageValue);
  };

  const handleLogin = () => {
    console.log('handleLogin');
    dispatch(login({
      email,
      password,
    }));
  };

  const handleRegister = () => {
    console.log('handleRegister');

    dispatch(
      registration({
        name,
        role,
        email,
        password,
      }),
    );
  };

  const handleKeyDown = (event: KeyboardEvent<HTMLInputElement>) => {
    if (event.key === 'Enter') {
      if (stage === Stage.LOGIN) {
        handleLogin();
        console.log('handleLogin');
      } else {
        handleRegister();
      }
    }
  };

  const handleContinueWithoutLogin = () => {
    console.log('handleContinueWithoutLogin');

    dispatch(setIsLocalUser(true));

    console.log('isLocalUser', isLocalUser);
  };

  return (
    <div
      className={s.container}
      id="container"
      onKeyDown={handleKeyDown}
    >

      {stage === Stage.LOGIN ? (
        <div className={s.auth}>
          <div className={s.auth__form}>
            <form
              className={s.auth__form__content}
              onSubmit={handleLogin}
            >
              <div className={s.auth__form__content__title}>
                Log In
              </div>

              <input
                className={`${s.auth__form__content__input} ${s.auth__form__content__input__email}`}
                type="email"
                placeholder="Email"
                onChange={(e) => setEmail(e.target.value)}
                value={email}
              />

              <input
                className={`${s.auth__form__content__input} ${s.auth__form__content__input__password}`}
                type="password"
                placeholder="Password"
                onChange={(e) => setPassword(e.target.value)}
                value={password}
                min={6}
                max={32}
              />

              <div className={s.auth__form__content__options}>
                <div className={s.auth__form__content__options__remember}>
                  <input
                    className={s.auth__form__content__options__remember__checkbox}
                    type="checkbox"
                  />

                  <div className={s.auth__form__content__options__remember__text}>
                    Remember me
                  </div>
                </div>

                <div className={s.auth__form__content__options__forgot}>
                  Forgot password?
                </div>
              </div>

              <button
                className={s.auth__form__content__button}
                type="submit"
              >
                Log In
              </button>

              <div className={s.auth__form__content__conn_with}>
                <div className={s.auth__form__content__conn_with__line} />

                <div className={s.auth__form__content__conn_with__text}>
                  Or Connect with
                </div>

                <div className={s.auth__form__content__conn_with__line} />
              </div>

              <div className={s.auth__form__content__social}>
                <div className={s.auth__form__content__social__item}>
                  <img
                    className={s.auth__form__content__social__item__img}
                    src={facebookLogo}
                    alt="facebook"
                  />
                </div>

                <div className={s.auth__form__content__social__item}>
                  <img
                    className={s.auth__form__content__social__item__img}
                    src={googleLogo}
                    alt="google"
                  />
                </div>
              </div>

              <div className={s.auth__form__continue}>
                <button
                  className={s.auth__form__continue__button}
                  type="button"
                  onClick={handleContinueWithoutLogin}
                >
                  <span className={s.auth__form__continue__button__text}>
                    Continue without login
                  </span>

                  <img src={manRight} alt="manRight" className={s.auth__form__continue__button__img} />
                  <img src={doorLogo} alt="doorLogo" className={s.auth__form__continue__button__img__door} />
                </button>
              </div>
            </form>
          </div>

          <div className={s.auth__welcome}>
            <div className={s.auth__welcome__content}>
              <div className={s.auth__welcome__content__logo}>
                <img src={plusPeople} alt="logo" />
              </div>

              <div className={s.auth__welcome__content__title}>
                Welcome to Bundoshki!
              </div>

              <div className={s.auth__welcome__content__text}>
                Register now and get access to all the features of the site.
              </div>

              <button
                className={s.auth__welcome__content__button}
                type="button"
                onClick={() => handleStageChange(Stage.REGISTER)}
              >
                <span
                  className={s.auth__welcome__content__button__text}
                >
                  Register
                </span>

                <img alt="arrow" src={arrowRight} className={s.auth__welcome__content__button__arrow} />
              </button>
            </div>
          </div>
        </div>
      ) : (
        <div className={`${s.auth} ${s.register}`}>
          <div className={`${s.auth__form} ${s.register__form}`}>
            <div className={s.auth__form__content}>
              <div className={s.auth__form__content__title}>
                Sign Up
              </div>

              <div className={s.auth__form__content__conn_with}>
                <div className={s.auth__form__content__conn_with__line} />

                <div className={s.auth__form__content__conn_with__text}>
                  Or
                </div>

                <div className={s.auth__form__content__conn_with__line} />
              </div>

              <div className={`${s.auth__form__content__social} ${s.register__form__content__social}`}>
                <div className={s.auth__form__content__social__item}>
                  <img
                    className={s.auth__form__content__social__item__img}
                    src={facebookLogo}
                    alt="facebook"
                  />
                </div>

                <div className={s.auth__form__content__social__item}>
                  <img
                    className={s.auth__form__content__social__item__img}
                    src={googleLogo}
                    alt="google"
                  />
                </div>
              </div>

              <input
                className={`${s.auth__form__content__input} ${s.auth__form__content__input__name}`}
                type="text"
                placeholder="name"
                onChange={(e) => setName(e.target.value)}
                value={name}
              />

              <input
                className={`${s.auth__form__content__input} ${s.auth__form__content__input__email}`}
                type="email"
                placeholder="Email"
                onChange={(e) => setEmail(e.target.value)}
                value={email}
              />

              <input
                className={`${s.auth__form__content__input} ${s.auth__form__content__input__password}`}
                type="password"
                placeholder="Password"
                onChange={(e) => setPassword(e.target.value)}
                value={password}
                min={6}
                max={32}
              />

              <div className={s.auth__form__content__options}>
                <div className={s.auth__form__content__options__remember}>
                  <input
                    className={s.auth__form__content__options__remember__checkbox}
                    type="checkbox"
                  />

                  <div className={s.auth__form__content__options__remember__text}>
                    Remember me
                  </div>
                </div>

                <div className={s.auth__form__content__options__forgot}>
                  Forgot password?
                </div>
              </div>

              <button
                className={s.auth__form__content__button}
                type="button"
                onClick={handleRegister}
              >
                Register
              </button>

              <div className={s.auth__form__continue}>
                <button
                  className={s.auth__form__continue__button}
                  type="button"
                  onClick={handleContinueWithoutLogin}
                >
                  <span className={s.auth__form__continue__button__text}>
                    Continue without login
                  </span>

                  <img src={manRight} alt="manRight" className={s.auth__form__continue__button__img} />
                  <img src={doorLogo} alt="doorLogo" className={s.auth__form__continue__button__img__door} />
                </button>
              </div>
            </div>
          </div>

          <div className={`${s.auth__welcome} ${s.register__welcome}`}>
            <div className={s.auth__welcome__content}>
              <div className={s.auth__welcome__content__logo}>
                <img src={exitDoor} alt="logo" />
              </div>

              <div className={s.auth__welcome__content__title}>
                Welcome Back!
              </div>

              <div className={s.auth__welcome__content__text}>
                To keep connected with us please login with your personal info
              </div>

              <button
                className={s.auth__welcome__content__button}
                type="button"
                onClick={() => handleStageChange(Stage.LOGIN)}
              >
                <img
                  alt="arrow"
                  src={arrowRight}
                  className={`${s.auth__welcome__content__button__arrow} ${s.register__welcome__content__button__arrow}`}
                />

                <span
                  className={s.auth__welcome__content__button__text}
                >
                  Login
                </span>
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};
