import React, { useEffect } from 'react';
import { EditingBlock } from './components/EditingBlock';
import { SelectorBar } from './components/SelectorBar';
import s from './SettingsPage.module.scss';
import { useAppDispatch } from '../../app/hooks';
import { setSelectedPage } from '../../features/main/mainSlice';
import { SelectedPage } from '../../types/selectedPage';
import { BurgerMenuButton } from '../../UI/BurgerMenuButton';

export const SettingsPage: React.FC = () => {
  const dispatch = useAppDispatch();

  useEffect(() => {
    dispatch(setSelectedPage(SelectedPage.SETTINGS));
  });

  return (
    <div>
      <header className={s.header}>
        <h1 className={s.header__title}>Settings</h1>

        <BurgerMenuButton />
      </header>

      <main className={s.settings}>
        <SelectorBar />

        <EditingBlock />
      </main>
    </div>
  );
};
