// import { AxiosResponse } from 'axios';
import $api from '../../http';
import { Product } from '../../types/products';

export default class ProductService {
  static async getProducts() {
    const response = await $api.get('/products');

    return response.data;
  }

  static async getProductsChunk(page: number, itemsPerPage: number, filter: string, searchQuery: string): Promise<Product[]> {
    const response = await $api.get('/products/chunk', {
      params: {
        page,
        itemsPerPage,
        filter,
        searchQuery,
      },
    });

    return response.data;
  }

  static async getProductsLength(filter: string, searchQuery: string) {
    const response = await $api.get('/products/length', {
      params: {
        filter,
        searchQuery,
      },
    });

    return response.data;
  }
}
