/* eslint-disable react/jsx-boolean-value */
/* eslint-disable no-lone-blocks */
/* eslint-disable eol-last */
/* eslint-disable no-multiple-empty-lines */
/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable jsx-a11y/control-has-associated-label */
/* eslint-disable no-console */
/* eslint-disable max-len */
/* eslint-disable react/void-dom-elements-no-children */
import React, { useEffect, useState } from 'react';
import { NavLink, useNavigate } from 'react-router-dom';
import { Swiper, SwiperSlide } from 'swiper/react';
import classNames from 'classnames';
import { Scrollbar } from 'swiper';
import { BurgerMenuButton } from '../../../../UI/BurgerMenuButton';
import s from './Header.module.scss';
import { ProductCategory, Product, ErrorType } from '../../../../types/products';
import { Example } from '../../../../hooks';
import 'swiper/scss';
import 'swiper/scss/navigation';
import 'swiper/scss/pagination';
import 'swiper/css/scrollbar';

import { useAppSelector, useAppDispatch } from '../../../../app/hooks';
import {
  setFilter,
  getProductsFailure,
  getProductsSuccess,
  getProductsStart,
  setProductsLength,
  setErrorText,
  setSearchQuery,
  calculateCartInfo,
} from '../../../../features/products/productsSlice';

import { setOrderMenuOpen } from '../../../../features/main/mainSlice';

import {
  getProductsChunk,
  getProductsLength,
} from '../../../../api/products';

import { getAndSetProducts } from '../../../../utils/getAndSetProducts';

// import required modules

export const Header: React.FC = () => {
  const {
    isMobile,
    isMobileMini,
    smallHight,
    is900,
    is1024,
  } = Example();

  const dispatch = useAppDispatch();
  const selectedFilter = useAppSelector((state) => state.products.selectedFilter);
  const currentPage = useAppSelector((state) => state.products.currentPage);
  const itemsPerPage = useAppSelector((state) => state.products.itemsPerPage);
  const searchQuery = useAppSelector((state) => state.products.searchQuery);
  const isOrderMenuOpen = useAppSelector((state) => state.main.isOrderMenuOpen);
  const localStorageCart = useAppSelector((state) => state.products.localStorageCart);
  const isUserAuth = useAppSelector((state) => state.users.isUserAuth);
  const cartItemsCount = useAppSelector((state) => state.products.cartItemsCount);
  const cartTotalPrice = useAppSelector((state) => state.products.cartTotalPrice);

  const navigate = useNavigate();

  const handleFilterClick = (filter: ProductCategory) => {
    console.log(filter);
    dispatch(setFilter(filter));

    getAndSetProducts(dispatch, 1, itemsPerPage, filter, searchQuery);
  };

  const handleSearch = (searchValue: string) => {
    console.log(searchValue);

    dispatch(setSearchQuery(searchValue));

    getAndSetProducts(dispatch, 1, itemsPerPage, selectedFilter, searchQuery);
  };

  // const handleBurgerOpen = () => {
  //   console.log('burger');

  //   navigate('/home/burger-menu', { replace: true });
  // };

  console.log(`isMobile ${isMobile}`);

  const handleOpenMobileSearch = () => {
    navigate('/home/search', { replace: true });
  };

  const handleOrderMenuOpen = () => {
    if (is1024) {
      navigate('/home/order-mobile', { replace: true });
    } else {
      dispatch(setOrderMenuOpen(true));
    }
  };

  return (
    <div className={s.header}>
      {isMobile ? (
        <div className={s.mobile_header__wrapper}>
          <div className={s.mobile_header__info}>
            <div className={s.mobile_header__title}>
              Bundoshki
            </div>
            <a
              href="tel:+380(99)0435879"
              type="phone:"
              className={s.mobile_header__date}
            >
              +380-(99)-043-58-79
            </a>
          </div>

          {!isMobileMini ? (
            <div className={s.mobile_button_position}>
              {!isOrderMenuOpen && (
                <>
                  {localStorageCart.length > 0 && (
                    <button
                      type="button"
                      className={s.home_page__main_order_opener}
                      onClick={handleOrderMenuOpen}
                    >
                      <div className={s.home_page__main_order_opener_item}>
                        <div className={s.home_page__main_order_opener_images} />
                        <div className={s.home_page__main_order_opener_text}>
                          {` ${cartItemsCount} items for total of ${cartTotalPrice.toFixed(2)} UAH`}
                        </div>

                        <div className={s.home_page__main_order_opener_button} />
                      </div>
                    </button>
                  )}
                </>
              )}

              <button
                type="button"
                className={s.mobile_header__search}
                onClick={handleOpenMobileSearch}
              />

              {smallHight && (
                <div className={s.burger__button}>
                  <BurgerMenuButton />
                </div>
              )}

            </div>
          ) : (
            <div className={s.mobile_header__buttons}>
              <button
                type="button"
                className={s.mobile_header__buttons__search}
                onClick={handleOpenMobileSearch}
              />

              <div className={s.burger__button}>
                <BurgerMenuButton />
              </div>
            </div>
          )}
        </div>
      ) : (
        <div className={s.header__wrapper}>
          <div className={s.header__info}>
            <div className={s.header__title}>
              Bundoshki
            </div>
            <a
              href="tel:+380(99)0435879"
              type="phone:"
              className={s.header__date}
            >
              +380-(99)-043-58-79
            </a>
          </div>

          {!isMobileMini && (
            <div className={s.tablet_button_position}>
              {!isOrderMenuOpen && (
                <>
                  {localStorageCart.length > 0 && (
                    <>
                      <button
                        type="button"
                        className={s.home_page__main_order_opener}
                        onClick={() => dispatch(setOrderMenuOpen(true))}
                      >
                        <div className={s.home_page__main_order_opener_item}>
                          <div className={s.home_page__main_order_opener_images} />
                          <div className={s.home_page__main_order_opener_text}>
                            {` ${cartItemsCount} items for total of ${cartTotalPrice.toFixed(2)} UAH`}
                          </div>
                          <div className={s.home_page__main_order_opener_button} />
                        </div>
                      </button>
                    </>
                  )}
                </>
              )}

              {smallHight ? (
                <>
                  <button
                    type="button"
                    className={s.mobile_header__search}
                    onClick={handleOpenMobileSearch}
                  />

                  <div className={s.burger__button}>
                    <BurgerMenuButton />
                  </div>
                </>
              ) : (
                <input
                  type="text"
                  className={classNames(
                    s.header__search,
                    { [s.header__search__order_menu_close]: !isOrderMenuOpen },
                  )}
                  placeholder="Search for food, coffee, etc.."
                  onChange={(event) => {
                    setTimeout(() => {
                      handleSearch(event.target.value);
                    }, 1500);
                  }}
                />
              )}
            </div>
          )}

        </div>
      )}
      <nav className={s.header__nav}>
        {
          !is900 ? (
            <>
              <ul className={s.header__nav__list}>
                <li className={s.header__nav__item}>
                  <button
                    type="button"
                    className={`${s.header__nav__item__button} ${selectedFilter === ProductCategory.ALL ? s.header__nav__item__button__active : ''}`}
                    onClick={() => handleFilterClick(ProductCategory.ALL)}
                  >
                    All positions
                  </button>
                </li>

                <li className={s.header__nav__item}>
                  <button
                    type="button"
                    className={`${s.header__nav__item__button} ${selectedFilter === ProductCategory.HOT_DISHES ? s.header__nav__item__button__active : ''}`}
                    onClick={() => handleFilterClick(ProductCategory.HOT_DISHES)}
                  >
                    Hot Dishes
                  </button>
                </li>

                <li className={s.header__nav__item}>
                  <button
                    type="button"
                    className={`${s.header__nav__item__button} ${selectedFilter === ProductCategory.COLD_DISHES ? s.header__nav__item__button__active : ''}`}
                    onClick={() => handleFilterClick(ProductCategory.COLD_DISHES)}
                  >
                    Cold Dishes
                  </button>
                </li>

                <li className={s.header__nav__item}>
                  <button
                    type="button"
                    className={`${s.header__nav__item__button} ${selectedFilter === ProductCategory.DRINKS ? s.header__nav__item__button__active : ''}`}
                    onClick={() => handleFilterClick(ProductCategory.DRINKS)}
                  >
                    Drinks
                  </button>
                </li>

                <li className={s.header__nav__item}>
                  <button
                    type="button"
                    className={`${s.header__nav__item__button} ${selectedFilter === ProductCategory.DESSERTS ? s.header__nav__item__button__active : ''}`}
                    onClick={() => handleFilterClick(ProductCategory.DESSERTS)}
                  >
                    Deserts
                  </button>
                </li>

                <li className={s.header__nav__item}>
                  <button
                    type="button"
                    className={`${s.header__nav__item__button} ${selectedFilter === ProductCategory.SALADS ? s.header__nav__item__button__active : ''}`}
                    onClick={() => handleFilterClick(ProductCategory.SALADS)}
                  >
                    Salads
                  </button>
                </li>

                <li className={s.header__nav__item}>
                  <button
                    type="button"
                    className={`${s.header__nav__item__button} ${selectedFilter === ProductCategory.SOUPS ? s.header__nav__item__button__active : ''}`}
                    onClick={() => handleFilterClick(ProductCategory.SOUPS)}
                  >
                    Soups
                  </button>
                </li>
              </ul>
            </>
          ) : (
            <ul className={s.header__nav__list_mobile}>
              <Swiper
                spaceBetween={3}
                slidesPerView={3}
                onSlideChange={() => console.log('slide change')}
                onSwiper={(swiper) => console.log(swiper)}
                scrollbar={{
                  hide: false,
                }}
                modules={[Scrollbar]}
                // centeredSlides
                style={{ width: '100%' }}
              >
                <SwiperSlide>
                  <li className={s.header__nav__item}>
                    <button
                      type="button"
                      className={`${s.header__nav__item__button} ${selectedFilter === ProductCategory.HOT_DISHES ? s.header__nav__item__button__active__mobile : ''}`}
                      onClick={() => handleFilterClick(ProductCategory.HOT_DISHES)}
                    >
                      Hot Dishes
                    </button>
                  </li>
                </SwiperSlide>
                <SwiperSlide>
                  <li className={s.header__nav__item}>
                    <button
                      type="button"
                      className={`${s.header__nav__item__button} ${selectedFilter === ProductCategory.COLD_DISHES ? s.header__nav__item__button__active__mobile : ''}`}
                      onClick={() => handleFilterClick(ProductCategory.COLD_DISHES)}
                    >
                      Cold Dishes
                    </button>
                  </li>
                </SwiperSlide>
                <SwiperSlide>
                  <li className={s.header__nav__item}>
                    <button
                      type="button"
                      className={`${s.header__nav__item__button} ${selectedFilter === ProductCategory.DRINKS ? s.header__nav__item__button__active__mobile : ''}`}
                      onClick={() => handleFilterClick(ProductCategory.DRINKS)}
                    >
                      Drinks
                    </button>
                  </li>
                </SwiperSlide>
                <SwiperSlide>
                  <li className={s.header__nav__item}>
                    <button
                      type="button"
                      className={`${s.header__nav__item__button} ${selectedFilter === ProductCategory.DESSERTS ? s.header__nav__item__button__active__mobile : ''}`}
                      onClick={() => handleFilterClick(ProductCategory.DESSERTS)}
                    >
                      Deserts
                    </button>
                  </li>
                </SwiperSlide>

                <SwiperSlide>
                  <li className={s.header__nav__item}>
                    <button
                      type="button"
                      className={`${s.header__nav__item__button} ${selectedFilter === ProductCategory.SALADS ? s.header__nav__item__button__active__mobile : ''}`}
                      onClick={() => handleFilterClick(ProductCategory.SALADS)}
                    >
                      Salads
                    </button>
                  </li>
                </SwiperSlide>

                <SwiperSlide>
                  <li className={s.header__nav__item}>
                    <button
                      type="button"
                      className={`${s.header__nav__item__button} ${selectedFilter === ProductCategory.SOUPS ? s.header__nav__item__button__active__mobile : ''}`}
                      onClick={() => handleFilterClick(ProductCategory.SOUPS)}
                    >
                      Soups
                    </button>
                  </li>
                </SwiperSlide>
              </Swiper>
            </ul>
          )
        }
      </nav>
    </div>
  );
};
