/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable max-len */
import { NavLink } from 'react-router-dom';
import { useState } from 'react';
import classNames from 'classnames';
import s from './Sidebar.module.scss';

import { useAppDispatch, useAppSelector } from '../../app/hooks';

import { logout } from '../../features/users/usersSlice';
import { SelectedPage } from '../../types/selectedPage';
import { setSelectedPage } from '../../features/main/mainSlice';

export const Sidebar: React.FC = () => {
  const dispatch = useAppDispatch();

  const selectedPage = useAppSelector((state) => state.main.selectedPage);

  const handlePageChange = (page: SelectedPage) => {
    dispatch(setSelectedPage(page));
  };

  const handleLogOut = () => {
    dispatch(logout());
  };

  return (
    <div className={s.sidebar}>
      <div className={s.sidebar__content}>
        <NavLink to="/home" className={s.sidebar__logo}>
          <div className={s.sidebar__logo__image} />
        </NavLink>

        <nav className={s.sidebar__nav}>
          <ul className={s.sidebar__nav__list}>

            <div className={`${s.sidebar__gap} `}>
              <div className={classNames(
                s.sidebar__gap__radius,
                { [s.sidebar__gap__radius__bottom_on]: selectedPage === SelectedPage.HOME },
              )}
              />
            </div>

            <button
              type="button"
              className={classNames(
                s.button__wrapper,
                { [s.button__wrapper_active]: selectedPage === SelectedPage.HOME },
              )}
              onClick={() => handlePageChange(SelectedPage.HOME)}
            >
              <li className={s.sidebar__nav__link}>
                <NavLink to="/home">
                  <div className={classNames(
                    s.sidebar__nav__logo,
                    { [s.sidebar__nav__logo_home_active]: selectedPage === SelectedPage.HOME },
                    { [s.sidebar__nav__logo_home]: selectedPage !== SelectedPage.HOME },
                  )}
                  />
                </NavLink>
              </li>
            </button>

            <div className={s.sidebar__gap}>
              <div className={classNames(
                s.sidebar__gap__radius,
                { [s.sidebar__gap__radius__bottom_on]: selectedPage === SelectedPage.DASHBOARD },
                { [s.sidebar__gap__radius__top_on]: selectedPage === SelectedPage.HOME },
                // { [s.sidebar__gap__radius__top_on]: active === 1 },
              )}
              />
            </div>

            <button
              type="button"
              className={classNames(
                s.button__wrapper,
                { [s.button__wrapper_active]: selectedPage === SelectedPage.DASHBOARD },
              )}
              onClick={() => handlePageChange(SelectedPage.DASHBOARD)}
            >
              <li className={`${s.sidebar__nav__link}`}>
                <NavLink to="/dashboard">
                  <div className={classNames(
                    s.sidebar__nav__logo,
                    { [s.sidebar__nav__logo_stats_active]: selectedPage === SelectedPage.DASHBOARD },
                    { [s.sidebar__nav__logo_stats]: selectedPage !== SelectedPage.DASHBOARD },
                  )}
                  />
                </NavLink>
              </li>
            </button>

            <div className={s.sidebar__gap}>
              <div className={classNames(
                s.sidebar__gap__radius,
                { [s.sidebar__gap__radius__bottom_on]: selectedPage === SelectedPage.NOTIFICATIONS },
                { [s.sidebar__gap__radius__top_on]: selectedPage === SelectedPage.DASHBOARD },
              )}
              />
            </div>

            <button
              type="button"
              className={classNames(
                s.button__wrapper,
                { [s.button__wrapper_active]: selectedPage === SelectedPage.NOTIFICATIONS },
              )}
              onClick={() => handlePageChange(SelectedPage.NOTIFICATIONS)}
            >
              <li className={`${s.sidebar__nav__link}`}>
                <NavLink to="/">
                  <div className={classNames(
                    s.sidebar__nav__logo,
                    { [s.sidebar__nav__logo_notification_active]: selectedPage === SelectedPage.NOTIFICATIONS },
                    { [s.sidebar__nav__logo_notification]: selectedPage !== SelectedPage.NOTIFICATIONS },
                  )}
                  />
                </NavLink>
              </li>
            </button>

            <div className={s.sidebar__gap}>
              <div className={classNames(
                s.sidebar__gap__radius,
                { [s.sidebar__gap__radius__bottom_on]: selectedPage === SelectedPage.SETTINGS },
                { [s.sidebar__gap__radius__top_on]: selectedPage === SelectedPage.NOTIFICATIONS },
              )}
              />
            </div>

            <button
              type="button"
              className={classNames(
                s.button__wrapper,
                { [s.button__wrapper_active]: selectedPage === SelectedPage.SETTINGS },
              )}
              onClick={() => handlePageChange(SelectedPage.SETTINGS)}
            >
              <li className={`${s.sidebar__nav__link}`}>
                <NavLink to="/settings/security">
                  <div className={classNames(
                    s.sidebar__nav__logo,
                    { [s.sidebar__nav__logo_settings_active]: selectedPage === SelectedPage.SETTINGS },
                    { [s.sidebar__nav__logo_settings]: selectedPage !== SelectedPage.SETTINGS },
                  )}
                  />
                </NavLink>
              </li>
            </button>

            <div className={s.sidebar__gap}>
              <div className={classNames(
                s.sidebar__gap__radius,
                { [s.sidebar__gap__radius__bottom_on]: false },
                { [s.sidebar__gap__radius__top_on]: selectedPage === SelectedPage.SETTINGS },
              )}
              />
            </div>

            <button
              type="button"
              className={classNames(
                s.button__wrapper,
                { [s.button__wrapper_active]: false },
                s.button__logout,
              )}
              onClick={() => handleLogOut()}
            >
              <li className={`${s.sidebar__nav__link}`}>
                <div className={classNames(
                  s.sidebar__nav__logo,
                  s.sidebar__nav__logo_exit_active,
                )}
                />
              </li>
            </button>

            <div className={s.sidebar__gap}>
              <div className={classNames(
                s.sidebar__gap__radius,
              )}
              />
            </div>
          </ul>
        </nav>
      </div>
    </div>
  );
};
