/* eslint-disable max-len */
/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable no-console */
/* eslint-disable padded-blocks */
// eslint-disable-next-line import/no-extraneous-dependencies
import {
  Navigate,
  Route,
  Routes,
  useNavigate,
} from 'react-router-dom';
import React, { useState, useEffect } from 'react';
import { Sidebar } from './components/Sidebar';
import { SearchPage } from './pages/SearchPage';
import { BurgerMenu } from './components/BurgerMenu';
import { DashboardPage } from './pages/DashboardPage';
import { ProductModal } from './modals/ProductModal';
import s from './App.module.scss';
import { HomePage } from './pages/HomePage';
import { OrderSecondStage } from './pages/HomePage/components/OrderSecondStage';
import { SettingsPage } from './pages/SettingsPage';
import { PageLoader } from './components/PageLoader';

import { getProducts, getProductsLength, getProductsChunk } from './api/products';
import ProductService from './api/services/ProductService';

import { useAppDispatch, useAppSelector } from './app/hooks';

import { ErrorType, Product } from './types/products';
import { OrderMobile } from './pages/HomePage/components/OrderMobile';

import {
  getProductsStart,
  getProductsSuccess,
  getProductsFailure,
  setProductsLength,
  setErrorText,
  setSearchQuery,
  setLocalStorageCart,
} from './features/products/productsSlice';
import { AuthPage } from './pages/AuthPage';
// import { ProductPage } from './pages/ProductPage';

import { getAndSetProducts } from './utils/getAndSetProducts';

import { checkAuth, setIsUsersLoading } from './features/users/usersSlice';
import { ProductManagement } from './pages/SettingsPage/components/EditingBlock/components/ProductManagement';
import { Security } from './pages/SettingsPage/components/EditingBlock/components/Security';
import { NotFoundPage } from './pages/NotFoundPage';
import { PaymentMobile } from './pages/PaymentMobile';

function App() {
  const dispatch = useAppDispatch();

  const currentPage = useAppSelector((state) => state.products.currentPage);
  const itemsPerPage = useAppSelector((state) => state.products.itemsPerPage);
  const selectedFilter = useAppSelector((state) => state.products.selectedFilter);
  const searchQuery = useAppSelector((state) => state.products.searchQuery);
  const isProductModalOpen = useAppSelector((state) => state.main.isProductModalOpen);

  const isUserAuth = useAppSelector((state) => state.users.isUserAuth);
  const isUsersLoading = useAppSelector((state) => state.users.isUsersLoading);

  const isLocalUser = useAppSelector((state) => state.users.isLocalUser);

  const navigate = useNavigate();

  useEffect(() => {
    navigate('/home');
  }, []);

  useEffect(() => {
    dispatch(setIsUsersLoading(true));

    getAndSetProducts(dispatch, currentPage, itemsPerPage, selectedFilter, searchQuery);

    if (!isUserAuth) {
      const cart = JSON.parse(localStorage.getItem('cart') as string);

      if (cart) {
        dispatch(setLocalStorageCart(cart));
      }
    }

    if (localStorage.getItem('token')) {
      console.log('check auth');

      dispatch(checkAuth()).then(() => {
        setTimeout(() => {
          dispatch(setIsUsersLoading(false));
        }, 1000);
      }).catch(() => {
        setTimeout(() => {
          dispatch(setIsUsersLoading(false));
        }, 1000);
      });
    } else {
      setTimeout(() => {
        dispatch(setIsUsersLoading(false));
      }, 1000);
    }
  }, []);

  return (
    <>
      {isUsersLoading ? (
        <PageLoader />
      ) : (
        <>
          {!isLocalUser && !isUserAuth ? (
            <AuthPage />
          ) : (
            <>
              <OrderSecondStage />
              {isProductModalOpen && <ProductModal />}
              <Routes>
                <Route
                  path="*"
                  element={(
                    <div className={s.app}>
                      <div className={s.app__wrapper}>
                        <div className={s.app__sidebar}>
                          <Sidebar />
                        </div>
                        <NotFoundPage />
                      </div>
                    </div>
                  )}
                />

                <Route path="/" element={<Navigate to="/home" replace />} />

                <Route
                  path="/home"
                  element={(
                    <div className={s.app}>
                      <div className={s.app__wrapper}>
                        <div className={s.app__sidebar}>
                          <Sidebar />
                        </div>
                        <HomePage />
                      </div>
                    </div>
                  )}
                />

                <Route
                  path="home/search"
                  element={(
                    <div className={s.app}>
                      <SearchPage />
                    </div>
                  )}
                />

                <Route
                  path="/home/order-mobile"
                  element={(
                    <div className={s.app}>
                      <OrderMobile />
                    </div>
                  )}
                />

                <Route
                  path="home/burger-menu"
                  element={(
                    <div className={s.app}>
                      <BurgerMenu />
                    </div>
                  )}
                />

                <Route
                  path="dashboard"
                  element={(
                    <div className={s.app}>
                      <div className={s.app__wrapper}>
                        <div className={s.app__sidebar}>
                          <Sidebar />
                        </div>
                        <DashboardPage />
                      </div>
                    </div>
                  )}
                />

                <Route
                  path="settings"
                  element={(
                    <div className={s.app}>
                      <div className={s.app__wrapper}>
                        <div className={s.app__sidebar}>
                          <Sidebar />
                        </div>
                        <SettingsPage />
                      </div>
                    </div>
                  )}
                >
                  <Route path=":security" element={<Security />} />
                  <Route path=":products-management" element={<ProductManagement />} />
                </Route>
              </Routes>
            </>
          )}
        </>
      )}
    </>
    // <PaymentMobile />
  );
}

export default App;
