/* eslint-disable max-len */
/* eslint-disable @typescript-eslint/no-unused-vars */
import React, { useState, useEffect } from 'react';
import classNames from 'classnames';
import { useNavigate } from 'react-router-dom';
import s from './HomePage.module.scss';
import { Header } from './components/Header';
import { Menu } from './components/Menu';
import { Order } from './components/Order';
// import productCardLogo from '../../images/logos/product_item-logo';
import { Example } from '../../hooks';
import { useAppSelector, useAppDispatch } from '../../app/hooks';
import { calculateCartInfo } from '../../features/products/productsSlice';

import {
  setOrderMenuOpen,
  setSelectedPage,
} from '../../features/main/mainSlice';

import { SelectedPage } from '../../types/selectedPage';

export const HomePage: React.FC = () => {
  const { isMobile, isMobileMini } = Example();
  const dispatch = useAppDispatch();
  const navigate = useNavigate();

  const isOrderMenuOpen = useAppSelector((state) => state.main.isOrderMenuOpen);
  const isUserAuth = useAppSelector((state) => state.users.isUserAuth);
  const cartItemsCount = useAppSelector((state) => state.products.cartItemsCount);
  const cartTotalPrice = useAppSelector((state) => state.products.cartTotalPrice);

  useEffect(() => {
    dispatch(setSelectedPage(SelectedPage.HOME));
    dispatch(calculateCartInfo({ isUserAuth }));
  });

  const handleOpenOrderMobile = () => {
    navigate('/home/order-mobile');
  };

  return (
    <div className={classNames(
      s.home_page,
      { [s.home_page__order_menu_close]: !isOrderMenuOpen },
    )}
    >
      <div className={s.home_page__main}>
        <Header />
        <Menu isOrderMenuOpen={isOrderMenuOpen} />

        {!isOrderMenuOpen && isMobileMini && cartItemsCount > 0 && (
          <button
            type="button"
            className={s.home_page__main_order_opener}
            onClick={handleOpenOrderMobile}
          >
            <div className={s.home_page__main_order_opener_item}>
              <div className={s.home_page__main_order_opener_images} />

              <div className={s.home_page__main_order_opener_text}>
                {` ${cartItemsCount} items for total of ${cartTotalPrice.toFixed(2)} UAH`}
              </div>

              <div className={s.home_page__main_order_opener_button}>
                <div className={s.home_page__main_order_opener_button_text}>
                  To Order
                </div>

                <img
                  className={s.home_page__main_order_opener_button_img}
                  src=""
                  alt=""
                />
              </div>
            </div>
          </button>
        )}
      </div>

      <div className={s.home_page__order}>
        <Order />
      </div>
    </div>
  );
};
