/* eslint-disable @typescript-eslint/no-unused-vars */
/* eslint-disable max-len */
/* eslint-disable jsx-a11y/heading-has-content */
/* eslint-disable jsx-a11y/control-has-associated-label */
import React, { useEffect } from 'react';
import { NavLink, useNavigate } from 'react-router-dom';

import s from './BurgerMenu.module.scss';
// import { Example } from '../../hooks';

export const BurgerMenu: React.FC = () => {
  const navigate = useNavigate();

  // useEffect(() => {
  //   window.history.replaceState({}, document.title, '/');
  // });

  const handlePageChange = (pageUrl: string) => {
    navigate(pageUrl, { replace: true });
  };

  const handleBurgerMenuClose = () => {
    navigate(-1);
  };

  return (
    <div className={s.burger_menu}>
      <header className={s.burger_menu__header}>
        <div className={s.burger_menu__header__close}>
          <div />

          <button
            type="button"
            className={s.burger_menu__header__logo_close}
            onClick={handleBurgerMenuClose}
          />
        </div>

        <div className={s.burger_menu__header__user__info}>
          <img
            src=""
            alt=")"
            className={s.burger_menu__header__user__info__img}
          />

          <h1 className={s.burger_menu__header__user__info__name}>
            Alex Anisimov
          </h1>
        </div>
      </header>

      <main className={s.burger_menu__main}>
        <ul className={s.burger_menu__main__ul}>
          <NavLink
            to="/home"
            style={{ textDecoration: 'none' }}
            className={s.burger_menu__main__ul__item__active}
          // onClick={() => handlePageChange('/home')}
          >
            <li className={`${s.burger_menu__main__ul__item}`}>
              <div className={`${s.burger_menu__main__ul__item__img} ${s.burger_menu__main__ul__item__img__home}`} />
              <p className={s.burger_menu__main__ul__item__link}>
                Home
              </p>
            </li>
          </NavLink>

          <button
            type="button"
            style={{ textDecoration: 'none' }}
            onClick={() => handlePageChange('/dashboard')}
          >
            <li className={`${s.burger_menu__main__ul__item}`}>
              <div className={`${s.burger_menu__main__ul__item__img} ${s.burger_menu__main__ul__item__img__dashboard}`} />

              <p className={s.burger_menu__main__ul__item__link}>Dashboard</p>
            </li>
          </button>

          <button
            type="button"
            style={{ textDecoration: 'none' }}
            onClick={() => handlePageChange('/notifications')}
          >
            <li className={`${s.burger_menu__main__ul__item} ${s.burger_menu__main__ul__item__notification}`}>
              <div className={`${s.burger_menu__main__ul__item__img} ${s.burger_menu__main__ul__item__img__notification}`} />

              <div className={s.burger_menu__main__ul__item__wrapper}>
                <a className={s.burger_menu__main__ul__item__link} href="/">Notification</a>

                <div className={s.burger_menu__main__ul__item__notification__count}>
                  322
                </div>
              </div>
            </li>
          </button>

          <button
            type="button"
            style={{ textDecoration: 'none' }}
            onClick={() => handlePageChange('/settings/security')}
          >
            <li className={`${s.burger_menu__main__ul__item}`}>
              <div className={`${s.burger_menu__main__ul__item__img} ${s.burger_menu__main__ul__item__img__settings}`} />

              <p className={s.burger_menu__main__ul__item__link}>Settings</p>
            </li>
          </button>
        </ul>
      </main>

      <footer className={s.burger_menu__footer}>
        <li className={`${s.burger_menu__main__ul__item}`}>
          <div className={`${s.burger_menu__main__ul__item__img} ${s.burger_menu__main__ul__item__img__exit}`} />

          <p className={`${s.burger_menu__main__ul__item__link} ${s.burger_menu__main__ul__item__link__exit}`}>Log out</p>
        </li>
      </footer>
    </div>
  );
};
